<template>
  <div>
    <el-row :gutter="10">
      <el-input v-model="name" placeholder=""></el-input>
      <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
    </el-row>
    <hr>
    <el-table :data="tableData">
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="addr" label=""></el-table-column>
      <el-table-column prop="mobile" label=""></el-table-column>
      <el-table-column prop="level" :formatter="formatLevel" label=""></el-table-column>
      <el-table-column prop="status" :formatter="formatStatus" label=""></el-table-column>
      <el-table-column prop="ctime" label=""></el-table-column>
      <el-table-column prop="id" label="">
        <template slot-scope="scope">
          <router-link :to="{name:'PlaceAdd',params:{id: scope.row.id, row: scope.row}}">
            <el-button type="primary" size="small"></el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <pagination v-on:getPageData="getTablePageData" :total-num="totalNum"></pagination>
  </div>
</template>

<script>
  export default {
    name: 'PlaceList',
    data() {
      return {
        name: '',
        tableData: [],
        totalNum: 0
      }
    },
    methods: {
      getTablePageData(pagerObj) {
        let params = {
          name: this.name,
          currentPage: pagerObj.currentPage,
          pageSize: pagerObj.pageSize
        }
        console.log(params)
        let that = this
        that.$axios.get('/admin/place/list', {params: params}).then(function (res) {
          console.log(`ok`)
          if (res.status === 200 && res.data.code === 200) {
            that.tableData = res.data.data.tableData
            that.totalNum = res.data.data.totalNum
          } else {
            that.tableData = []
            that.totalNum = 0
          }
        }).catch((error) => {
          console.log(`err: ${error}`)
          that.tableData = []
          that.totalNum = 0
        })
      },
      search() {
        let params = {
          name: this.name
        }
        console.log(params)
        let that = this
        that.$axios.get('/admin/place/list', {params: params}).then(function (res) {
          console.log(`ok`)
          if (res.status === 200 && res.data.code === 200) {
            that.tableData = res.data.data.tableData
            that.totalNum = res.data.data.totalNum
          } else {
            that.tableData = []
            that.totalNum = 0
          }
        }).catch((error) => {
          console.log(`err: ${error}`)
          that.tableData = []
          that.totalNum = 0
        })
      },
      del(val, index) {
        let that = this
        that.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          that.$axios.get('/admin/user/delete/' + val).then(function (res) {
            if (res.status === 200 && res.data.code === 200) {
              that.tableData.splice(index, 1)
              that.$message({type: 'success', message: '!'})
            } else {
              that.$message({type: 'error', message: ''})
            }
          })
        }).catch(() => {
          that.$message({type: 'info', message: ''})
        })
      },
      formatLevel(row, column, cellValue) {
        let text = ''
        let level = row.level
        if (level === 1) {
          text = '1A'
        } else if (level === 2) {
          text = '2A'
        } else if (level === 3) {
          text = '3A'
        } else if (level === 4) {
          text = '4A'
        } else if (level === 5) {
          text = '5A'
        }
        return text
      },
      formatStatus(row, column, cellValue) {
        let text = ''
        let status = row.status
        if (status === 1) {
          text = ''
        } else if (status === 5) {
          text = ''
        }
        return text
      }
    },
    mounted() {
      let that = this
      that.$axios.get('/admin/place/list').then(function (res) {
        if (res.status === 200 && res.data.code === 200) {
          that.tableData = res.data.data.tableData
          that.totalNum = res.data.data.totalNum
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-input {
    display: inline-block;
    width: 140px;
  }

  .el-select {
    width: 100px;
  }

</style>



