<template>
  <div>
    <el-row :gutter="10">
      <el-input v-model="name" placeholder=""></el-input>
      <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
    </el-row>
    <hr>
    <el-table :data="tableData">
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="mobile" label=""></el-table-column>
      <el-table-column prop="sex" :formatter="formatSex" label=""></el-table-column>
      <el-table-column prop="mark" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <a :href="scope.row.authPic" target="_blank">
            <img :src="scope.row.authPic" style="max-width: 100px;height: 50px"/>
          </a>
        </template>
      </el-table-column>
      <el-table-column prop="status" :formatter="formatStatus" label=""></el-table-column>
      <el-table-column prop="auditorName" label=""></el-table-column>
      <el-table-column prop="auditTime" label=""></el-table-column>
      <el-table-column prop="time" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="authStatus(scope.row.id, scope.$index, 'pass')"></el-button>
          <el-button type="danger" size="small" @click="authStatus(scope.row.id, scope.$index, 'nopass')"></el-button>
          <router-link :to="{name:'Password',params:{id: scope.row.id, mobile: scope.row.mobile}}">
            <el-button type="warning" size="small" title=""></el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <pagination v-on:getPageData="getTablePageData" :total-num="totalNum"></pagination>
  </div>
</template>

<script>
  export default {
    name: 'MerchantUserList',
    data() {
      return {
        name: '',
        tableData: [],
        totalNum: 0
      }
    },
    methods: {
      getTablePageData(pagerObj) {
        let params = {
          name: this.name,
          currentPage: pagerObj.currentPage,
          pageSize: pagerObj.pageSize
        }
        this.query(this, params)
      },
      search() {
        let params = {
          name: this.name
        }
        this.query(this, params)
      },
      authStatus(val, index, status) {
        let that = this
        if (!val || !status) {
          return that.$message({type: 'info', message: ''})
        }
        that.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          that.$axios.get('/admin/user/auth?id=' + val + '&status=' + status).then(function (res) {
            if (res.status === 200 && res.data.code === 200) {
              if (status === 'pass') {
                that.tableData[index].status = 1
              } else {
                that.tableData[index].status = 5
              }
              that.$message({type: 'success', message: '!'})
            } else {
              that.$message({type: 'error', message: ''})
            }
          })
        }).catch(() => {
          that.$message({type: 'info', message: ''})
        })
      },
      formatStatus(row, column, cellValue) {
        let text = ''
        let status = row.status
        if (status === 1) {
          text = ''
        } else if (status === 5) {
          text = ''
        } else {
          text = ''
        }
        return text
      },
      formatSex(row, column, cellValue) {
        let text = ''
        let sex = row.sex
        if (sex === 1) {
          text = ''
        } else if (sex === 2) {
          text = ''
        } else if (sex === 3) {
          text = ''
        }
        return text
      },
      query(that, params) {
        console.log(params)
        that.$axios.get('/admin/user/merchantUserList', {params: params}).then(function (res) {
          console.log(`ok`)
          if (res.status === 200 && res.data.code === 200) {
            that.tableData = res.data.data.tableData
            that.totalNum = res.data.data.totalNum
          } else {
            that.tableData = []
            that.totalNum = 0
          }
        }).catch((error) => {
          console.log(`err: ${error}`)
          that.tableData = []
          that.totalNum = 0
        })
      }
    },
    mounted() {
      this.query(this, {})
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-input {
    display: inline-block;
    width: 140px;
  }

  .el-select {
    width: 100px;
  }

</style>



